.catalog {
  display: flex;
  flex-wrap: wrap;

  max-width: 944px;
  margin: 0 auto;

  &__card {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 48px;
    transition: transform 300ms;
    transition-duration: 300ms;
  }
  
  &:hover {
    color: #34568b;
    transform: scale(1.2);
  }
}
